










































































import {
	mapState
} from 'vuex'
import amap from '@/common/amap-wx.js';
import {
	getReqest
} from '@/util/network.js'

export default {
	data() {
		return {
			background: ['color1', 'color2', 'color3'],
			items: [1, 2, 3, 3, 5, 6, 6, 7],
			amapPlugin: null,
			key: '72239a17febe0f534f11c5b1fbd8ce4c',
			addressName: '获取中...',
			banners: [],
			shops:[]
		}
	},
	methods: {
		
		getNearShops() {
			const params = {
				latitude: '20.111111',
				longitude: '113.09091',
				page: 1,
				pageSize: 10
			}
			const that = this;
			getReqest('shop/nearShops', params, res => {
				console.log('res shop/nearShops###', res);
				that.shops = res.content;
			})
		},
		getBanner() {
			const params = {
				latitude: '20.111111',
				longitude: '113.09091'
			}
			const that = this;
			getReqest('banner/list', params, function(res) {
				that.banners = res;
				console.log('res banner/list###', res);
			})
		},
		gotoConfirmOrder(){
			uni.navigateTo({
				url: "../order/makeSureOrder/MakeSureOrder"
			})
		},
		gotoAddAddress(){
			uni.navigateTo({
				url: "../address/addAddress"
			})
		},
		gotoAddresslist(){
			uni.navigateTo({
				url: "../address/chooseAddress"
			})
		},
		goSearch() {
			uni.navigateTo({
				url: "../HM-search/HM-search"
			})
		},
		goShop(shop) {
			console.log(shop);
			uni.navigateTo({
				url: "shop/shop?shopId="+shop.id
			})
		},
		getRegeo() {
			var that = this;
			uni.showLoading({
				title: '获取信息中'
			});
			this.amapPlugin.getRegeo({
				success: (data) => {
					console.log(data)
					that.addressName = data[0].name;
					console.log("addressName ", that.addressName)
					uni.hideLoading();
				},
				fail: e => {
					uni.hideLoading();
					console.log(e)
				}
			});
		}
	},
	computed: mapState(['forcedLogin', 'hasLogin', 'userName']),
	 onLoad() {
		this.amapPlugin = new amap.AMapWX({
			key: this.key
		});
		if (!this.hasLogin) {
			uni.showModal({
				title: '未登录',
				content: '您未登录，需要登录后才能继续',
				/**
				 * 如果需要强制登录，不显示取消按钮
				 */
				showCancel: !this.forcedLogin,
				success: (res) => {
					if (res.confirm) {
						/**
						 * 如果需要强制登录，使用reLaunch方式
						 */
						if (this.forcedLogin) {
							uni.reLaunch({
								url: '../login/login'
							});
						} else {
							uni.navigateTo({
								url: '../login/login'
							});
						}
					}
				}
			});
		}
		this.getRegeo();
		this.getBanner();
		this.getNearShops();
		
	}
}
